import React, { useState, useRef } from 'react';
import '../css/comment.css';

function Commentwrite({product_info, onCreate}) {
    const contentInput = useRef();
    const authorInput = localStorage.getItem('authId');

    const [state, setState] = useState({
        author:authorInput,
        content: "",
        rating: "",
    });

    const handleChangeState = (e) => {
        setState({
            ...state,
            [e.target.name]: e.target.value,
        });
    };  

    const handleSubmit = () => {
        if(state.content.length < 5) {
            contentInput.current.focus();
            alert(' 5  .');
            return;
        }
        if(state.rating < 1) {
            alert('5  ?! ?');
            return;
        }
        onCreate(state.author, state.content, state.rating);
        setState({
            author:authorInput,
            content:"",
            rating:"0",
        });
        console.log(state);
        alert(' ');
    };

    return (
    <div className="reviewEditor">
    <div>
    <div className="commentBox">
    <div>
    </div>
        <textarea
            className="reviewWrite"
            ref={contentInput}
            name="content" 
            value={state.content} 
            onChange={handleChangeState}
            placeholder="  !"
            />
    </div>
    <div className="commentSubmit">
    <div>
        <select 
            name='rating'
            value={state.rating} 
            onChange={handleChangeState}>
            <option value={0}>
             
            </option>    
            <option value={1}>
            
            </option>
            <option value={2}>
            
            </option>
            <option value={3}>
            
            </option>
            <option value={4}>
            
            </option>
            <option value={5}>
            
            </option>
        </select>
    </div>
    <div>
        <button type="button" className="btn-primary btn-55" onClick={handleSubmit} id="reviewSubmit"> </button>
    </div>
    </div>
    </div>
    </div>
    );
}

export default Commentwrite;