/*    -  */ 
const INCREASE = 'INCREASE';
const DECREASE = 'DECREASE';
const ADD_TO_CART = 'ADD_TO_CART';
const PICK_THIS_ITEM = 'PICK_THIS_ITEM';
const VIEW_CNT1 = 'VIEW_CNT1';
const VIEW_CNT2 = 'VIEW_CNT2';


/*    -  */
export const increase = () => ({ type: INCREASE });
export const decrease = () => ({ type: DECREASE });
export const addToCart = item => ({
  type: ADD_TO_CART,
  item 
});
export const pickItem = item => ({
  type: PICK_THIS_ITEM,
  item 
});
export const viewCnt1 = count => ({
  type: VIEW_CNT1,
  count
});

export const viewCnt2 = count => ({
  type: VIEW_CNT2,
  count
});

/*      */
//  (      .)
const initialState = {
  id:0,
  count: 0,
  view : 0,
  name: '',
  list: [],
  listPick: [],
  board1: {count : 0},
  board2: {count : 0}
};

/*   
//       
//     .
// :     ! */

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case "INCREASE":
      return {
        ...state,
        count: state.count + 1
      };
    case "DECREASE":
      return {
        ...state,
        count: state.count - 1
      };
    case "ADD_TO_CART":
      return {
        ...state,
        list: state.list.concat(action.item)
      };
    case "PICK_THIS_ITEM":
        return {
          ...state,
          listPick: state.listPick.concat(action.item)
        };
    case "VIEW_CNT1":
        return {
          ...state,
          board1: state.board1.count  + 1
        };
    case "VIEW_CNT2":
        return {
          ...state,
          board2: state.board1.count  + 1
        };
    default:
      return state;
  }
}