import { useEffect } from 'react';
import Slider from "react-slick";
import ProductUnit from './cardUI/productUnitActivity';
import "../css/slider.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import styled from 'styled-components';
import NextArrow from './slider/nextArrow';


const StyledSlider = styled(Slider)`
width:100%;
height:500px;
overflow:hidden;
margin-bottom:30px;
`;

export default function SimpleSlider({data, setCart, closeCart, likeModalClose, store, reverse, cart}) {
  useEffect(() => {
    console.log(data);
},[]);

const settings = {
  slide:"frame",
  infinite: true,
  speed: 500,
  slidesToScroll: 6,
  slidesToShow: 6,
  nextArrow: <NextArrow />,
  swipeToSlide: true,
  responsive: [ //    
  {  
    breakpoint: 1024, 
    settings: {
      //   ,    
      slidesToShow:3,
      slidesToScroll: 1
    } 
  },
  {  
    breakpoint: 960, //  960px
    settings: {
      //   ,    
      slidesToShow:2,
      slidesToScroll: 1
    } 
  },
  { 
    breakpoint: 768, //  768px
    settings: {	
      //   ,    
      slidesToShow:1,
      slidesToScroll: 1
    } 
  }]};

    return (
      <div>
        <StyledSlider {...settings}>
                  {data.map((d) => (<ProductUnit item={d} setCart={setCart} closeCart={closeCart} closeLike={likeModalClose} store={store} reverse={reverse} cart={cart}/>))}
        </StyledSlider>
      </div>
             );
           }