import axios from "axios";
import { useState, useRef } from 'react';
import '../../css/update.css';
import Input from '../tinyForm/input';
import BlogInput from '../tinyForm/blogInput';

function update({serverURL}) {
    /*          */
    const uid = localStorage.getItem('authId');
    const blogId = localStorage.getItem('blogId');

    const rpwd = useRef();
    const remail = useRef();
    const raddress = useRef();
    const rnick= useRef();
    const rblogtit = useRef();
    const rblogcon = useRef();

    const [ userState, setState ] = useState({
        pass : "",
        email:"",
        address: "",
        nickname: "",
    }); 

    const [ blogState, blogsetState ] = useState({
        blogTit : "",
        blogContents:"",
    }); 

    /*   -   */
    const DeleteUserInfo = event => {
        event.preventDefault();
        axios.delete(`${serverURL}/user/${uid}`).then(res => {
            console.log(res);
            location.replace('http://localhost:3000/');
        }).catch(error => {
            console.log('error');
        }).then(() => {
            console.log('delete user');
        })} 

    /*   -     */
    const setBlogUser = event => {
        event.preventDefault();
        /*     
        axios.post(``).then(res => {
            console.log(res);
        }).catch(error => {
            console.log('error');
        }).then(() => {
            console.log('post data');
        }) */
    } 
    
    /*   -     */
    const UpdateUserInfo = event => {
            event.preventDefault();
            setState({
                pass : rpwd.current.value || '',
                email: remail.current.value || '',
                address: raddress.current.value || '',
                nickname: rnick.current.value || '',
            });

            axios.put(`${serverURL}/user/${uid}`,JSON.stringify(userState)).then(res => {
                console.log(res);
                location.replace('http://localhost:3000/');
            }).catch(error => {
                console.log('error');
            }).then(() => {
                console.log('update user info');
            })} 

    /*   -      */
    const UpdateBlogUserInfo = event => {
        event.preventDefault();
        blogsetState({
            blogTit : rblogtit.current.value || '',
            blogContents: rblogcon.current.value || '',
        });
        axios.put(`${serverURL}/user/${uid}`,JSON.stringify(blogState)).then(res => {
            console.log(res);
            location.replace('http://localhost:3000/');
        }).catch(error => {
            console.log('error');
        }).then(() => {
            console.log('update user info - blog user');
        })
    }
        return (
            <div className="container-fluid">
               <div className="row">
                    <div className="col-12">
                        <div className="section">
                            <h1 className="page-tit">  </h1>
                            <div className="update-sec">
                            <div className="update-sec-first">
                                <span className="signup-title update" id="update-first-tit"> </span>
                                <span className="signup-title update"> </span>
                            </div>
                            <div className="update-sec-last">
                            <form className="blog-write-form" method="POST">
                                <div className="write-inputs">
                                <div className="first-inputs">
                                <div className="uid"><strong></strong> <Input type={'text'} name={'id'} /></div>
                                <div className="upass"><strong></strong><Input type={'password'} name={'password'} ref={rpwd} /></div>
                                <div className="umail"><strong></strong><Input type={'text'} name={'email'} ref={remail}/></div>
                                <div className="uaddress"><strong></strong><Input type={'text'} name={'address'} ref={raddress}/></div>
                                <div className="unick"><strong></strong><Input type={'text'} name={'nick'} ref={rnick}/></div>
                                <div className="upload-btn-sec1">
                                    <button className="btn-outlined btn-55" onClick={DeleteUserInfo}> </button>
                                    <button className="btn-primary btn-55" onClick={UpdateUserInfo}>  </button>
                                </div>
                                </div>
                                </div>
                            </form>
                                </div>
                                </div>
                                <BlogInput rblogtit={rblogtit} rblogcon={rblogcon} UpdateBlogUserInfo={UpdateBlogUserInfo} />
                                </div>
                                {/*blogId ? (<BlogInput rblogtit={rblogtit} rblogcon={rblogcon} UpdateBlogUserInfo={UpdateBlogUserInfo} />) : (<button className="btn-primary btn-55" id="blogCreate" onClick={setBlogUser}> </button>)*/}
                                </div>
                                
                                </div>
                                </div>
        )
    }

export default update;